import React from "react";
import { MDXProvider } from "@mdx-js/react";
import { MDXRenderer } from 'gatsby-plugin-mdx';
import ExpoSnack from 'react-expo-snack';

import { AnchorLink } from "../base/links";
import { Text } from "../base/typography";
import { Heading } from "../base/typography";
import Image from "../base/image";

const MarkdownWrapper = ({children, options}) => {
    return (
        <>
            <MDXProvider 
                
                components={{
                h2: ({node, ...props}) => <Heading {...props} level={2} noMargin />,
                h3: ({node, ...props}) => <Heading {...props} level={3} noMargin />,
                h4: ({node, ...props}) => <Heading {...props} level={4} noMargin />,
                h5: ({node, ...props}) => <Heading {...props} level={5} noMargin />,
                p: ({node, ...props}) => <Text {...props} noTopMargin={!options.p.topMargin} />,
                a: AnchorLink,
                img: ({node, ...props}) => <Image {...props} />,
                // pre: CodeBlock,
                ExpoSnack
                }}
            >
                <MDXRenderer>
                    {children}
                </MDXRenderer>
            </MDXProvider>
        </>
    )
};

MarkdownWrapper.defaultProps = {
    options: {
        p: {
            topMargin: true
        }
    }
}

export default MarkdownWrapper;


