import React from "react";
import { graphql } from 'gatsby';
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { solid } from '@fortawesome/fontawesome-svg-core/import.macro';

import Page from "../../components/layout/page";
import { LayoutContainer, LayoutItem, LayoutItemWrapper, LayoutRow } from "../../components/layout/layout";
import MarginContainer from "../../components/layout/margin-container";
import { TextLink } from "../../components/base/links";
import { Heading } from "../../components/base/typography";
import MarkdownWrapper from "../../components/markdown/markdown-wrapper";

export default function BlogDetail ({data}) {
  const headerImage = getImage(data.strapiPost.cover.localFile.childImageSharp.gatsbyImageData)
  return (
    <>
      <Page 
        active="blog"
        title={data.strapiPost.SEO.metaTitle}
        description={data.strapiPost.SEO.metaDescription}
        image={data.strapiPost.cover.url}
      >
      <LayoutContainer>   
        <LayoutRow>
          <LayoutItemWrapper 
            rowCenter
          >
            <LayoutItem 
              fullWidth 
              maxWidth={'40rem'} 
            >
              <TextLink to="/blog"><FontAwesomeIcon icon={solid('chevron-left')} /> Back to blog posts</TextLink>
              <MarginContainer topBase >
                <GatsbyImage image={headerImage} alt={data.strapiPost.cover.alternativeText}/>
              </MarginContainer>
              <Heading level={2} marginTop>{data.strapiPost.title}</Heading>
              <MarkdownWrapper
                options={{
                  p: {
                    topMargin: false
                  }
                }}
              >
              {data.strapiPost.content.data.childMdx.body}
              </MarkdownWrapper>
            </LayoutItem>
          </LayoutItemWrapper>
        </LayoutRow>   
        </LayoutContainer>
      </Page>
    </>
  )
};

export const query = graphql`
query getBlogPost($slug: String){
  strapiPost(slug: { eq: $slug }) {
    title
    cover {
      alternativeText
      url
      localFile {
        childImageSharp {
          gatsbyImageData
        }
      }
    }
    content {
      data {
        childMdx {
          body
        }
      }
    }
    SEO {
      metaDescription
      metaTitle
    }
  }
}
`