import React from "react";
import styled from "styled-components";

// For now only used in blog detail page. Centers and scales image 

const Image = ({src, alt}) => {
    return (
        <StyledImageWrapper>
            <StyledImage src={src} alt={alt} />
        </StyledImageWrapper>
    )
}

const StyledImage = styled.img`
    width: auto;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
`

const StyledImageWrapper = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    margin: var(--spacing-m) 0;
    max-height: var(--img-m);
    width: 100%;
`;

export default Image;